@mixin tx-style {
    font-size: $fSize1;
    text-align: justify;
    display: table-cell;
    vertical-align: middle;
    @media only screen and (max-width: 960px) {
        font-size: 1.2em; } }

@mixin a-hover ($fontS) {
    color: black;
    font-weight: 500;
    font-size: $fontS;
    margin-right: 2em;
    border-bottom: $brd $clr3;
    padding-top: -10px;

    &:hover {
        color: $clr3;
        border-bottom: $brd black;
        transition-property: color, border-bottom-color;
        transition-duration: .3s;
        transition-timing-function: linear;
        text-decoration: none;
        outline: none; } }

$fSize1: 1.5em;
$fSize2: 1.8em;
$brd: 3px solid;
$clr1: #607bf3;
$clr2: #1b3b92;
$clr3: #0084ff;


.main-menu {
    position: sticky;
    top: 0px;
    z-index: 5;
    background: #fff;
    padding-bottom: 10px;

    a {
        @include a-hover ($fSize1); } }

.tx1-1 {
    border: $brd $clr1;
    padding: 20px;
    font-size: $fSize2;
    text-align: justify;
    @media only screen and (max-width: 960px) {
        font-size: 1.3em;
        padding: 10px; } }

.tx1-2 {
    border-top: $brd $clr2;
    border-bottom: $brd $clr2;
    color: black;
    align-self: end;
    @include tx-style; }


.tx2-1 {
    border-top: $brd $clr3;
    border-right: $brd $clr3;
    margin-top: 0;
    line-height: 2em;
    font-weight: 500;
    @include tx-style;
    @media only screen and (max-width: 960px) {
        font-weight: 400; } }

.col.tx2-2 {
    color: $clr3;
    padding-left: 2em;
    padding-right: 2em;
    @include tx-style;
    @media only screen and (max-width: 960px) {
        padding-left: 1em;
        padding-right: 1em; } }


.tx2-3 {
    font-weight: 600;
    text-shadow: 1px 1px 2px black;
    letter-spacing: 2px;
    line-height: 1.5em;
    padding-top: 3px;
    background: repeating-linear-gradient(-45deg, #0000ff35, #0000ff35 5px, transparent 8px, transparent 15px);

    @include tx-style;

    &:hover {
        background: #00000000;
        transition-property: background-color;
        transition-duration: 1s;
        transition-timing-function: linear;
        border: $brd $clr3;
        padding-left: 12px;
        padding-right: 12px;
        padding-top: 0px; }

    @media only screen and (max-width: 960px) {
        background: #0000ff35; } }




.img1 {
    max-height: 200px; }

.tx3-1 {
    border-left: $brd $clr1;
    border-right: $brd $clr1;
    @include tx-style; }

.footer-text {
    font-size: 1.2em;
    font-weight: 400;
    letter-spacing: 2px;
    @media only screen and (max-width: 960px) {
        font-size: 1em; } }

.footer-a {
    @include a-hover (1em); }

div.breakline {
    border: 1px solid #00000035;
    width: 90%;
    margin-top: 2em;
    margin-left: 40px; }
